<script>
    import questionList from '../components/question/questionList'
    import * as config from '../../unit/config'
    import {mapGetters} from 'vuex'

    export default {
        name: "question",
        components: {
            questionList
        },
        data() {
            return {
                /**
                 * 
                 * */
                a: '',
                b: '',
                c: ''
            }
        },
        created() {
            let that = this;
        },
        mounted() {
            /**
             *  
             * */
            wx.ready(() => {
                wx.hideOptionMenu();
            });
        },
        methods: {
            /**
             * questionList
             * */
            listeraSingleVal(data) {
                let that = this;
                that.a = data;
            },
            listerbSingleVal(data) {
                let that = this;
                that.b = data;
            },
            listercSingleVal(data) {
                let that = this;
                that.c = data;
            },
            /**
             * 
             * */
            async submit() {
                let that = this;
                that.$showLoading();
                if (9 === that.a.size + that.b.size + that.c.size) {
                    let data = {};
                    data[that.a.type] = that.a.sum;
                    data[that.b.type] = that.b.sum;
                    data[that.c.type] = that.c.sum;
                    console.log(``);
                    console.log(data);
                    /**
                     * 
                     * */
                    let result = that.getResult(data);
                    console.log(`${result}`);

                    /**
                     * 
                     * */
                    await that.$store.dispatch('setFinalResult', {
                        im: config.requestInterface.setfinalresult,
                        fps: {
                            'open_id': that.openidInfo.open_id,
                            'answer': result
                        },
                        url: config.CONST_FETCH_REQUEST_URI
                    });

                    console.log(`${that.setFinalResultInfo}`);

                    if (that.setFinalResultInfo) {
                        that.$hideLoading();
                        that.$router.replace({name: 'result'})
                        // that.$router.push({name: 'result'});
                    }
                } else {
                    that.$hideLoading();
                    alert('')
                }

            },
            /**
             * 
             * */
            getResult(data) {
                let string = '';

                // 
                let result = Array.from(Object.values(data));

                // 
                let min = Math.min(...result);

                // 
                let max = Math.max(...result);

                if (max === min) {
                    return 'AB'
                } else {
                    for (let k in data) {
                        if (data[k] === max) {
                            string += k
                        }
                    }
                    return string
                }
            }
        },
        computed: {
            /**
             * 
             * true  / false 
             * */
            // valSum() {
            //     let that = this;
            //     if (9 === that.a.size + that.b.size + that.c.size) {
            //         return false
            //     }
            //     return true
            // },
            /**
             * 
             * */
            ...mapGetters([
                'openidInfo',
                'setFinalResultInfo'
            ])
        }
    }
</script>

